@import '../../variables'

.marketing-tag-div
    width: 100%
    height: 60vh
    
.col-one-headcontent
    width: 100%
    height: 60vh
    display: flex
    flex-direction: column
    justify-content: center
    align-items: center
    background-color: #ffffff

.main-content-1
    margin-bottom: 1rem

.main-content-2
    margin-bottom: 3rem

.contact-details
    display: flex
    justify-content: space-around
    padding-bottom: 5rem
    background-color: #ffffff
    box-shadow: 0px 0px 6px #0000001a

.dilivery
    width: 22%
    height: 15rem
    display: flex
    align-items: center
    border-bottom: 0.3rem solid red

.dilivery img
    width: 10rem
    height: 6rem
    margin: 2rem

.email
    width: 22%
    height: 15rem
    display: flex
    align-items: center
    border-bottom: 0.3rem solid red

.email img
    width: 10rem
    height: 10rem
    margin: 1rem

.phone
    width: 22%
    height: 15rem
    display: flex
    align-items: center
    border-bottom: 0.3rem solid red

.phone img
    width: 8rem
    height: 8rem
    margin: 2rem

.whats-app
    width: 22%
    height: 15rem
    display: flex
    align-items: center
    border-bottom: 0.3rem solid red

.whats-app img
    width: 8rem
    height: 8rem
    margin: 2rem

.email-data
    margin-top: 1rem

.call-data
    margin-top: 1rem

.whatsapp-data
    margin-top: 1rem

.product-top-div
    padding-bottom: 4rem
    background-color: #F7F7F7

.list-of-product-text
    width: 100%
    height: 20vh
    display: flex
    justify-content: center
    align-items: center

.list-of-product-span
    padding: 3rem
    border-radius: 2rem
    background-color: $icon-red

.aline-home-items-center
    width: 100%
    min-height: 47vh
    display: flex
    justify-content: space-between
    position: relative
    justify-content: center
