@import '../../variables'
    
.all-the-details-of-about-us-center
    display: flex
    flex-direction: column
    justify-content: center
    align-items: center

.image-div-about-us
    width: 100%
    height: 45rem
    background-color: pink

.about-us-details-center-container
    width: 100%

.about-us-details-center
    width: 100%
    height: 35rem
    display: flex
    justify-content: center
    flex-direction: column
    align-items: center
    margin-top: 10rem
    margin-bottom: 5rem
    

.our-history-details-center-container
    width: 100%

.our-history-details-center
    width: 100%
    height: 35rem
    display: flex
    justify-content: center
    flex-direction: column
    align-items: center
    background-color: light-grey
    margin-top: 8rem
    margin-bottom: 5rem

.title-about-text
    width: 15%
    height: 5.5rem
    display: flex
    justify-content: center
    border-bottom: 0.3rem solid $icon-red
    margin-bottom: 3rem
   

.our-value-text
    width: 100%
    display: flex
    justify-content: center

.topic-data-width
    width: 50%
    margin-top: 2rem

.topic-data-width p
    text-align: center
    line-height: 2rem

.our-values-details-center-container
    width: 100%
    height: 35rem
    display: flex
    flex-direction: column
    justify-content: center
    aline-items: center

    margin-bottom: 8rem
    margin-top: 8rem


.value-multi-column
    width: 100%
    
    display: flex
    justify-content: center
    align-items: center


.width-of-value-div
    width: 100%
    margin-top: 2rem
    display: flex
    justify-content: center
    align-items: center

.our-value-sub-topic
    display: flex
    justify-content: center

.value-multi-column p
    text-align: center
    line-height: 2rem
    padding: 2.5rem

.row-width-size
    width: 75%
    