@import '../../variables'

.enquiry-form-div
    width: 55rem
    height: 55rem
    display: flex
    justify-content: center
    align-items: center
    flex-direction: column
    padding: 3rem
    // box-shadow: 0rem .3rem .6rem #00000029
    // background-color: $icon-red
    margin: 2.5rem
    border-radius: 1rem

.contact-form-text-div
    width: 50rem
    height: 4rem
    display: flex
    align-items: center
    justify-content: center
    margin-bottom: 2rem

.contact-form-button-div
    width: 50rem
    height: 4rem
    display: flex
    align-items: center
    justify-content: center

.enquiry-input-box
    width: 50rem
    height: 5rem
    border-radius: 1rem
    margin: 1rem 0rem
    border: 0.2rem solid $icon-red

.enquiry-textarea-box
    width: 50rem
    height: 20rem
    border-radius: 1rem
    margin: 1.5rem 0rem
    border: 0.2rem solid $icon-red

.enquiry-submit-button
    width: 15rem
    height: 5rem
    border-radius: 1rem
    margin-top: 3rem
    background-color: $icon-red



    