@import '../../../variables'

.single-product-container-div
    width: 100rem
    height: 40.5rem
    margin: 1.5rem
    display: flex
    justify-content: center
    align-items: center
    border-radius: 1.5rem
    background-color: #fff
    box-shadow: 0rem .3rem .6rem #00000029
    position: relative
    @media (max-width: 375px)
        width: 31.5rem
        height: 61.0rem
    @media (min-width: 376px) and (max-width: 411px)
        width: 40.0rem
        height: 61.0rem

.background-image-position
    position: absolute
    height: 100%
    width: 100%
    border-radius: 1.5rem
    opacity: 0.3

.title-name
    margin-bottom: 2.0rem
    font-size: 2.2rem

.aline-col-element
    width: 100%
    height: 32.5rem
    display: flex
    position: relative
    
    justify-content: center
    align-items: center

.aline-col-element h3
    margin-top: -1.5rem
    margin-bottom: 1.0rem

.product-img-style
    width: 20rem
    height: 20rem
    padding: 1rem
    border-radius: 1.5rem
    display: flex
    justify-content: center
    align-items: center
    box-shadow: .0rem .0rem .6rem $icon-red
    background-color: white
    z-index: 2
    margin-bottom: 2rem

.product-img-style img
    width: 18.0rem
    height: 15.0rem

.separate-key-value
    display: flex
    justify-content: center
    margin-bottom: 1.5rem

.key-style
    font-size: 2rem
    font-weight: 500

.key-value
    font-size: 2rem
    margin-left: 2.0rem

.product-details-style
    height: 32.5rem
    display: flex
    position: relative
    z-index: 1
    justify-content: center
    align-items: center

.product-image img
    width: 70%
    height: 70%
    position: relative
    z-index: 1
    right: 1rem

.delivery-container
    width: 100rem
    height: 100rem
   

.center-product-image-and-delivery
    display: flex
    flex-direction: column
    justify-content: center
    align-items: center
    
    border-radius: 1rem

.packaging-text
    display: flex
    height: 10rem
    flex-direction: column
    justify-content: center
    align-items: center

.packaging-text p
    word-spacing: .6rem

.packaging-text-contact-benifit
    display: flex
    height: 25rem
    flex-direction: column
    justify-content: center
    align-items: center
    

.packaging-text-contact-benifit p
    width: 90%
    text-align: center
    word-spacing: .6rem
    margin-bottom: 2rem
    line-height: 3rem