@import url("https://cdnjs.cloudflare.com/ajax/libs/meyer-reset/2.0/reset.min.css")
@import url('https://fonts.googleapis.com/css2?family=Nanum+Myeongjo:wght@800&display=swap')
@import url("https://fonts.googleapis.com/css?family=Archivo:400,300,700|Source+Sans+Pro:400")
@import url("https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100&display=swap")
@import './variables'

*
  margin: 0
  padding: 0
  box-sizing: border-box
  font-family: "Jost", sans-serif

html
  font-size: 62.5%

a
  text-decoration: none

li
  list-style: none

body
  width: 100%
  min-height: 100vh
  background-color: white
  overflow-x: hidden

/* width */
::-webkit-scrollbar 
  width: 10px
  height: auto

/* Track */
::-webkit-scrollbar-track 
  border-radius: 10px


/* Handle */
::-webkit-scrollbar-thumb 
  background: $silver 
  border-radius: 10px


/* Handle on hover */
::-webkit-scrollbar-thumb:hover 
  background: $silver

input
  outline: none
  border: none
  padding-left: 1rem

textarea
  outline: none
  border: none
  padding-left: 1rem
  padding-top: 1rem

button
  outline: none
  border: none
